<template>
    <div class="goods">
        <div class="menu-wrapper" ref="menuWrapper">
            <ul>
                <li v-for="(item, index) in goods" class="menu-item" :class="{'current':currentIndex===index}" @click="selectMenu(index, $event)">
                    <span class="text b-border-1px">
                        <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
                    </span>
                </li>
            </ul>
        </div>
        <div class="foods-wrapper" ref="foodsWrapper">
            <ul>
                <li v-for="item in goods" class="food-list food-list-hook">
                    <h1 class="title">{{item.name}}</h1>
                    <ul>
                        <li @click="selectFood(food, $event)" v-for="food in item.foods" class="food-item b-border-1px">
                            <div class="icon">
                                <img width="57" height="57" :src="food.icon">
                            </div>
                            <div class="content">
                                <h2 class="name">{{food.name}}</h2>
                                <p class="desc">{{food.description}}</p>
                                <div class="extra">
                                    <span class="count">月售{{food.sellCount}}份</span><span class="rating">好评率{{food.rating}}%</span>
                                </div>
                                <div class="price">
                                    <span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                                </div>
                                <div class="cartcontrol-wrapper">
                                    <cartcontrol :food="food" ></cartcontrol>
                                </div>
                            </div>
                        </li>
                    </ul>
                </li>
            </ul>
        </div> 
        <shopcart ref="shopcart" :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shopcart>  
        <transition name="fade">
            <div class="list-mask" v-show="maskShow" @click="_maskHide"></div>
        </transition>
        <food :food="selectedFood" ref="food"></food> 
    </div>
</template>

<script type="text/ecmascript-6">
    import BScroll from 'better-scroll';
    import shopcart from '../../components/shopcart/shopcart';
    import cartcontrol from '../../components/cartcontrol/cartcontrol';
    import { eventHub } from '../../eventHub';
    import food from '../../components/food/food';
    import {getGoods} from '@api/api.js';

    const ERR_OK = 0;

    export default {
        props: {
            seller: {
                type: Object
            }
        },
        data() {
            return {
                goods: [],
                listHeight: [],
                scrollY: 0,
                maskShow: false,
                selectedFood: {}
            };
        },
        computed: {
            currentIndex() {
                for (let i = 0; i < this.listHeight.length; i++) {
                    let height1 = this.listHeight[i];
                    let height2 = this.listHeight[i + 1];
                    if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
                        return i;
                    }
                }
                return 0;
            },
            selectFoods() {
                let foods = [];
                this.goods.forEach((good) => {
                    good.foods.forEach((food) => {
                        if (food.count) {
                            foods.push(food);
                        }
                    });
                });
                return foods;
            }
        },
        created() {
            this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];

            // 获取 goods 数据
            this._getGoods();

            // 监听 cart.add 事件
            eventHub.$on('cart.add', this._drop);
            // 监听 cart.decrease 事件
            eventHub.$on('cart.decrease', this._judgeTotalCount);
            // 监听 mask.toggle 事件
            eventHub.$on('mask.toggle', this._maskToggle);
        },
        beforeDestory() {
            // 监听 cart.add 事件
            eventHub.$off('cart.add', this._drop);
            // 监听 cart.decrease 事件
            eventHub.$off('cart.decrease', this._judgeTotalCount);
            // 监听 mask.toggle 事件
            eventHub.$off('mask.toggle', this._maskToggle);
        },
        methods: {
            _getGoods() {
                getGoods().then((response) => {
                    response = response.body;
                    // console.log('goods====>', response);
                    if (response.errno === ERR_OK) {
                        this.goods = response.data;
                        // DOM 还没有更新
                        this.$nextTick(() => {
                           this._initScroll();
                           this._calculateHeight();
                        });
                    }
                });
            },
            selectMenu(index, event) {
                if (!event._constructed) {
                    return;
                }
                let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
                let el = foodList[index];
                this.foodsScroll.scrollToElement(el, 300);
            },
            selectFood(food, event) {
                if (!event._constructed) {
                    return;
                }
                this.selectedFood = food;
                this.$refs.food.show();
            },
            // 抛物线小球
            _drop(target) {
                // 调用 shopcart组件 drop 方法
                this.$refs.shopcart.drop(target);
            },
            // mask toggle
            _maskToggle(showstate) {
                // 设置 maskShow
                this.maskShow = !showstate;
            },
            _maskHide() {
                this.maskShow = false;
                this.$refs.shopcart.hideList();
            },
            _judgeTotalCount() {
                 // 调用 shopcart组件 totalCount
                let totalCount = this.$refs.shopcart.totalCount;
                if (totalCount === 0 && this.maskShow) {
                    this._maskHide();
                }
            },
            _initScroll() {
                this.meunScroll = new BScroll(this.$refs.menuWrapper, {
                    click: true
                });

                this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
                    click: true,
                    probeType: 3
                });
                // 监听 滚动事件
                this.foodsScroll.on('scroll', (pos) => {
                    this.scrollY = Math.abs(Math.round(pos.y));
                });
            },
            _calculateHeight() {
                let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
                let height = 0;
                this.listHeight.push(height);
                for (let i = 0; i < foodList.length; i++) {
                    let item = foodList[i];
                    height += item.clientHeight;
                    this.listHeight.push(height);
                }
            }
        },
        components: {
            shopcart,
            cartcontrol,
            food
        }
    };
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @import "../../common/stylus/mixin"

    .goods
        display: flex
        position: absolute
        top: 174px
        bottom: 46px
        width: 100%
        overflow: hidden
        .menu-wrapper
            flex: 0 0 80px
            width: 80px
            background: #f3f5f7
            .menu-item
                display: table
                height: 54px
                width: 56px
                padding: 0 12px
                line-height: 14px
                &.current
                    position: relative
                    z-index: 10
                    margin-top: -1px
                    background: #fff
                    font-weight: 700
                    .text
                        border-none()
                .text
                    display: table-cell
                    width: 56px
                    vertical-align: middle
                    b-border-1px(rgba(7, 17, 27, 0.1))
                    font-size: 12px
                    .icon
                        display: inline-block
                        width: 12px
                        height: 12px
                        background-size: 12px 12px
                        background-repeat: no-repeat
                        &.decrease
                            bg-image("./img/decrease_3")
                        &.discount
                            bg-image("./img/discount_3")
                        &.guarantee
                            bg-image("./img/guarantee_3")
                        &.invoice
                            bg-image("./img/invoice_3")
                        &.special
                            bg-image("./img/special_3")
                
        .foods-wrapper
            flex: 1
            .food-list
                .title
                    padding-left: 14px
                    height: 26px
                    line-height: 26px
                    border-left: 2px solid #d9dde1
                    font-size: 12px
                    color: rgb(147, 153, 159)
                    background: #f4f5f7
                .food-item
                    display: flex
                    margin: 18px
                    padding-bottom: 18px
                    b-border-1px(rgba(7, 17, 27, 0.1))
                    &:last-child
                        border-none()
                        margin-bottom: 0
                    .icon
                        flex: 0 0 57px
                        margin-right: 10px
                    .content
                        flex: 1
                        .name
                            margin: 2px 0 8px 0
                            height: 14px
                            line-height: 14px
                            font-size: 14px
                            color: rgb(7, 17, 27)
                        .desc, .extra
                            line-height: 10px
                            font-size: 10px
                            color: rgb(147, 153, 159)
                        .desc
                            line-height: 12px
                            margin-bottom: 8px
                        .extra
                            .count
                                margin-right: 12px
                        .price
                            font-weight: 700
                            line-height: 24px
                            .now
                                margin-right: 8px
                                font-size: 14px
                                color: rgb(240, 20, 20)
                            .old
                                text-decoration: line-through
                                font-size: 10px
                                color: rgb(147, 153, 159)     
                        .cartcontrol-wrapper
                            position: absolute
                            right: 0
                            bottom: 12px
        
        .list-mask
            position: fixed
            top: 0
            left: 0
            width: 100%
            height: 100%
            z-index: 40
            background: rgba(7, 17, 27, 0.6)
            backdrop-filter: blur(10px) // 磨砂效果
            filter: blur(10px)
            &.fade-enter, &.fade-leave-to
                opacity: 0
                background: rgba(7, 17, 27, 0)
            &.fade-enter-active, &.fade-leave-active
                transition: all 0.5s
            &.fade-enter-to
                opacity: 1
                background: rgba(7, 17, 27, 0.6)
        
        
        
</style>



