<template>
    <div class="cartcontrol" v-cloak>
        <transition name="move">
            <div class="cart-decrease" v-show="food.count" @click.stop.prevent="decreaseCart">
                <span class="inner icon-remove_circle_outline"></span>
            </div>
        </transition>
        <div class="cart-count" v-show="food.count>0">{{food.count}}</div>
        <div class="cart-add icon-add_circle" @click.stop.prevent="addCart"></div>
    </div>
</template>

<script type="text/ecmascript-6">
    import Vue from 'vue';
    import { eventHub } from '../../eventHub';
    
    export default {
        props: {
            food: {
                type: Object
            }
        },
        methods: {
            addCart(event) {
                if (!event._constructed) {
                    return;
                }
               if (!this.food.count) {
                    Vue.set(this.food, 'count', 1);
               } else {
                this.food.count++;
               }
               // console.log('event.target====>', event.target);
               // 点击时 分发 cart.add 事件
               eventHub.$emit('cart.add', event.target);
            },
            decreaseCart(event) {
                if (!event._constructed) {
                    return;
                }
                if (this.food.count) {
                    this.food.count--;
                }
                // 点击时 分发 cart.decrease 事件
               eventHub.$emit('cart.decrease', event.target);
            }
        }
    };
</script>

<style lang="stylus" rel="stylesheet/stylus">
    [v-cloak] 
        display: none
    
    .cartcontrol
        font-size: 0
        .cart-decrease
            display: inline-block
            padding: 6px
            .inner
                display: inline-block
                line-height: 24px
                font-size: 24px
                color: rgb(0, 160, 220)
         .cart-decrease
            &.move-enter-active, &.move-leave-active
                transition: all 0.4s linear
                opacity: 1
                transform: translate3d(0, 0, 0)
                .inner
                    transition: all 0.4s linear
                    transform: rotate(0)
            &.move-enter, &.move-leave-to
                opacity: 0
                transform: translate3d(24px, 0, 0)
                .inner
                    transform: rotate(180deg)
        .cart-count
            display: inline-block
            vertical-align: top
            width: 12px
            padding-top: 6px
            line-height: 24px
            text-align: center
            font-size: 10px
            color: rgb(147, 153, 159)
        .cart-add
            display: inline-block
            padding: 6px
            line-height: 24px
            font-size: 24px
            color: rgb(0, 160, 220)
</style>



